import React from 'react';
import commentaries from '../db/commentaries.json';
import { ShortCommentary } from './feed/ShortCommentary';
import { FullCommentary } from './feed/FullCommentary';
export class App extends React.Component {
    constructor() {
        super(...arguments);
        this.state = {
            isShow: false,
            currentCommentary: {}
        };
        this.showCommentary = (item) => {
            this.setState({
                isShow: true,
                currentCommentary: item
            });
        };
        this.closeCommentary = () => {
            this.setState({
                isShow: false
            });
        };
    }
    render() {
        const { isShow, currentCommentary } = this.state;
        return (React.createElement("div", { className: 'container feed' },
            commentaries.map((item, index) => (React.createElement(ShortCommentary, { key: index, item: item, showFull: this.showCommentary }))),
            isShow ? React.createElement(FullCommentary, { item: currentCommentary, close: this.closeCommentary }) : null));
    }
}
