import React from 'react';
export class ShortCommentary extends React.Component {
    render() {
        const { item, showFull } = this.props;
        return (React.createElement("div", { className: 'commentary', onClick: () => showFull(item) },
            React.createElement("div", { className: 'commentary__label' }, item.label),
            React.createElement("div", { className: 'commentary__date' }, item.date)));
    }
}
